import React, { useState, useEffect } from "react";
import '../../styles/index.scss';
import WhiteNoise from './images/whitenoise.png';

const IMAGES = new Array(18).map((i,index)=>index+1);
let index = 1;

export default function Background() {
  const [imgsLoaded, setImgsLoaded] = useState(false);
  const [intervalId, setIntervalId] = useState(null);

  useEffect(() => {
    const loadImage = image => {
      return new Promise((resolve, reject) => {
        const loadImg = new Image()
        loadImg.src = require(`./images/${image}.svg`)
        loadImg.onload = () => {
          resolve(image.url);
        }

        loadImg.onerror = err => reject(err)
      })
    }

    Promise.all(IMAGES.map(image => loadImage(image)))
      .then(() => {
        let intervalId = setInterval(changeBackground, 100);
        setTimeout(openOverlay, 1000);
        setIntervalId(intervalId);
        setImgsLoaded(true);
        const container = document.querySelector("#backgroundContainer");
        console.log(`zzz finished loading images`);
        if(container) container.classList.add('show');
      })
      .catch(err => console.log("zzz Failed to load images", err))

      return(() => {
        clearInterval(intervalId);
      })
  }, []);

  const changeBackground = () => {
    const img = document.querySelector("#bgImage");
    if(img)
      img.src = require(`./images/${index}.svg`);
    index = index===18 ? 1 : index+1;
  }

  const openOverlay = () => {
    const container = document.getElementById('backgroundContainer');
    if(container) container.classList.add("blur");
    const overlay = document.getElementById('backgroundOverlay');
    if(overlay) overlay.classList.add("active");
    const nav = document.getElementById('nav');
    if(nav) nav.classList.add("active");
  }

  //REPLACE THIS WITH A PROPER LOADER
  if(!imgsLoaded) {
    return (
      <div>Loading</div>
    )
  }
  
  return (
      <>
        <div className="backgroundContainer" id="backgroundContainer">
            <img src={require(`./images/${1}.svg`)} id="bgImage" alt="logo" />
        </div>
        <div id="backgroundOverlay"></div>
        <img id="whitenoise" src={WhiteNoise} alt="noise" />
      </>
  );
}
